import { defineComponent, ref, computed } from 'vue';
import { useRoute } from 'vue-router';
import { gsap } from 'gsap';
import { useStore } from '@/store';
import { mapMenu } from '@/utils';
import { mitt } from '@/services';
export default defineComponent({
  components: {},
  setup: function setup() {
    var store = useStore();
    var route = useRoute();
    var imgSize = ref('60px');
    var defaultUserMenuStyle = ref({
      backgroundColor: '#545c64',
      textColor: '#fff',
      activeTextColor: '#ffd04b',
      router: true,
      collapseTransition: false
    });
    var imgRef = ref();
    var isCollapse = ref(false);
    mitt.on('isFold', function (pay) {
      isCollapse.value = pay;

      if (isCollapse.value) {
        gsap.to('.image', {
          duration: 0.5,
          width: 30
        });
        gsap.to('.title', {
          duration: 0.5,
          opacity: 0
        });
      } else {
        gsap.to('.image', {
          duration: 0.5,
          width: 60
        });
      }
    }); // 

    var defaultActive = ref(mapMenu.firstMenu.url); //     elsubmenu  type 1 index 

    var handleOpen = function handleOpen(subitem) {
      mitt.emit('handleOpen', subitem); // console.log(mapMenu.firstMenu)

      var a = mapMenu.findByPath(store.state.loginModule.userMenuList, route.path);
      defaultActive.value = a.url;
    };

    var handleClose = function handleClose(subitem) {
      mitt.emit('handleClose', subitem); // console.log(subitem)
    };

    var handleSelect = function handleSelect(item) {
      mitt.emit('handleSelect', item); // console.log(item)
    };

    var userMenuList = computed(function () {
      return store.state.loginModule.userMenuList;
    });
    return {
      userMenuList: userMenuList,
      defaultUserMenuStyle: defaultUserMenuStyle,
      isCollapse: isCollapse,
      defaultActive: defaultActive,
      handleOpen: handleOpen,
      handleClose: handleClose,
      handleSelect: handleSelect,
      imgSize: imgSize,
      imgRef: imgRef
    };
  }
});