import { defineComponent, ref } from 'vue';
import NavHeader from '@/components/nav-header';
import NavAside from '@/components/nav-aside';
import { mitt } from '@/services';
import gsap from 'gsap/all';
export default defineComponent({
  components: {
    NavAside: NavAside,
    NavHeader: NavHeader
  },
  setup: function setup() {
    var isFold = ref(false);
    mitt.on('isFold', function (pay) {
      isFold.value = pay;
    });

    var enter = function enter(el, done) {
      gsap.from(el, {
        duration: 1,
        y: -600,
        opacity: 0,
        ease: 'back.inOut(1.9)',
        onComplete: done
      });
    };

    var leave = function leave(el, done) {
      gsap.to(el, {
        duration: 1,
        x: 600,
        opacity: 0,
        onComplete: done
      });
    };

    return {
      isFold: isFold,
      enter: enter,
      leave: leave
    };
  }
});