import { useStore } from '@/store';
import { useRoute } from 'vue-router';
export default defineComponent({
  name: 'breadcrumb',
  setup: function setup() {
    var store = useStore();
    var route = useRoute();
    var breadArrs = ref([]);
    onMounted(function () {
      var defautItem = mapMenu.findByPath(userMenuList, route.path); // path item  firstmenu  .

      breadArrs.value.fill(defautItem);
